                <router-link class="dropdown-item" :to="{name:'FactorForm', params:{ factorType: 'buy'}}">  </router-link>
                <router-link class="dropdown-item" :to="{name:'FactorForm', params:{ factorType: 'backFromBuy'}}">    </router-link>
                <router-link class="dropdown-item" :to="{name:'FactorForm', params:{ factorType: 'sale'}}">  </router-link>
                <router-link class="dropdown-item" :to="{name:'FactorForm', params:{ factorType: 'backFromSale'}}">    </router-link>
