            <div class="dropdown-divider"></div>
            <router-link class="dropdown-item" :to="{name:'List', params:{ form: 'factor', type: 'buy'}}"> </router-link>
            <router-link class="dropdown-item" :to="{name:'List', params:{ form: 'factor', type: 'backFromBuy'}}">   </router-link>
            <router-link class="dropdown-item" :to="{name:'List', params:{ form: 'factor', type: 'sale'}}"> </router-link>
            <router-link class="dropdown-item" :to="{name:'List', params:{ form: 'factor', type: 'backFromSale'}}">   </router-link>
            <div class="dropdown-divider"></div>
