          </div>
        </li>
        <li class="nav-item dropdown">
          <a class="nav-link dropdown-toggle" data-toggle="dropdown" href="#"></a>
          <div class="dropdown-menu dropdown-menu-right " aria-labelledby="navbarDropdown">
            <router-link class="dropdown-item" :to="{name:'BalanceReport'}"></router-link>
            <div class="dropdown-divider"></div>
            <router-link class="dropdown-item" :to="{name:'LedgerReport'}">   </router-link>
            <div class="dropdown-divider"></div>
            <router-link class="dropdown-item" :to="{name:'JournalReport'}"> </router-link>
            <div class="dropdown-divider"></div>
            <router-link class="dropdown-item" :to="{name:'IncomeStatementReport'}">  </router-link>
            <div class="dropdown-divider"></div>
            <router-link class="dropdown-item" :to="{name:'BalanceSheetReport'}"></router-link>
            <div class="dropdown-divider"></div>
            <router-link class="dropdown-item" :to="{name:'InventoryReport'}"> </router-link>
          </div>
